.day-weather{
    text-align: center;
    font-size: 3.5vmin;
    min-width: 40%;
    cursor: pointer;
    padding: 5px;
    transition: 1s ease all;
    border-radius: 15px;
}

.day-weather:hover{
    background: #222;
}

@media screen and (min-width: 600px) and (orientation: landscape) {
    .day-weather {
        margin-bottom:5%;
    }
  }