.app{
    width: 100vw;
    height: 100vh;
   
    position: relative;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}

.error-message{
    text-align: center;
    position: absolute;
    bottom: 3%;
    font-size: 2vmax;
}

.dark{
    background: #000;
    color: #fff;
}

.light{
    background: #fff;
    color: #000;
}

.light .weather:hover , .light .day-weather:hover{
    background: #eee;
}

@media screen and (min-width:600px) and (orientation:landscape){
    .app{
        flex-direction: row;
        
    }
}