.days-weather {
  width: 100%;
  height: 30vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: scroll;
}

@media screen and (min-width: 600px) and (orientation: landscape) {
  .days-weather {
    width:25%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-direction: column;
    justify-content: flex-start;
    position: absolute;
    right: 0;
  }
}
