.menu-btn {
  width: auto;
  height: auto;
  background: #ccc;
  padding: 0;
  position: absolute;
  left: 0;
  top: 3vh;
  transition: 1s ease all;
  color: #000;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 4;
  padding: 2%;
}

@media screen and (min-width: 600px) and (orientation: landscape) {
  .menu-btn {
            padding: 0 1%;
  }
  .close-btn.menu-btn {
    transform: translateX(30vw);
  }
}
