.setting {
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 11;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transform: translateY(-100%);
  transition: 1s ease all;
}

.show {
  transform: translateY(0);
}

.setting__holder {
  width: 25vw;
  height: 5vh;
  background: #aaa;
  border-radius: 20px;
  border: 2px solid #000;
}

.setting__holder--btn {
  width: 50%;
  height: 100%;
  background: #555;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.3s ease-out all;
  font-size: 2.5vmax;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.move {
  transform: translateX(100%);
}

.setting__buttons {
  width: 100%;
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.setting__buttons button {
  display: block;
  width: 60%;
  height: 7vh;
  font-size: 4vmax;
}

.setting p {
  font-size: 4vmax;
  text-align: center;
  margin-bottom: 3vh;
}

.setting__theme,
.setting__temp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 600px) and (orientation: landscape) {
  .setting {
    display: block;
  }
  .setting__theme,
  .setting__temp {
    display: inline-block;
    width: 50%;
    height: 80vh;
    padding-top: 35%;
  }

  .setting__theme > div,
  .setting__temp > div {
    margin: 0 auto;
  }

  .setting__buttons {
    position: absolute;
    bottom: 0;
  }
}
