.weather {
  text-align: center;
  font-size: 2.5vmax;
  height: auto;
  display: flex;
  padding: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 1s ease all;
  border-radius: 15px;
}

.weather:hover{
  background: #222;
}


.weather * {
  margin: 3% 0;
}

.icon {
  width: 35%;
  margin-bottom: 0;
}

@media screen and (min-width: 600px) and (orientation: landscape){
  .weather{
    height: 75vh;
  }
  .weather * {
    margin: 2% 0;
  }
}