.search {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 1s ease all;
    z-index: 1;
  }

.show {
  width: 100%;
  height: 100%;
    transform: translateX(0);
    
}

.hide{
    transform: translateX(-200%);
}

.search-city{
  width: 100%;
  height: 40%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.Search__dialog {
  font-size: 5vmin;
  border: 2px solid #000;
  padding: 3%;
  width: 80%;
}

.search__btn {
  font-size: 5vmin;
  padding: 3%;
  border: none;
  background: #bbb;
}

.search__title{
  font-size: 4vmax;
  margin-bottom:2rem ;

}

.light{
  background: #fff;
  color: #000;
}


@media screen and (min-width: 600px) and (orientation: landscape) {
 .search{
  width: 30%;
  left: 0;
 }
 .show{
  transform: translateX(0);
 }
 .hide{
  transform: translateX(-100%);
 }
 .Search__dialog{
  font-size: 2.5vmax;
  padding: 1vmin;
 }
 .search__title{
  font-size: 2.5vmax;
 
 }
}

.setting__btn{
  width: 80%;
  height: 7vh;
  font-size: 4vmax;
  margin-bottom: 2vh;
}