.hours-weather {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(100%);
  transition: 1s ease all;
  z-index: 5;
}

.show {
  transform: translateX(0);
}

.hour-weather {
  margin-top: 10vh;
}

.one-hour {
  text-align: center;
  font-size: 2vmax;
  padding: 10% 0;
  position: relative;
  }

.one-hour::after {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  bottom: 5%;
  left: 0;
}



.close {
  position: fixed;
  top: 5vh;
  right: 3vw;
  font-size: 3vmax;
  cursor: pointer;
  z-index: 6;
}

.hour-weather__icon{
  width: 25%;
  height: auto;
}

.hours-weather__date{
  margin-top: 10vh;
  font-size: 4vmax;
}


@media screen and (min-width: 600px) and (orientation: landscape) {
  .hour-weather {
    display: grid;
    width: 80%;
    grid-template: auto / 1fr 1fr 1fr;
    justify-content: center;
    gap: 2%;
  }

}


