.loading__dialog {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading {
  width: 80px;
  height: 80px;
  display: grid;
  grid-template: auto / 1fr 1fr;
}

.cube {
  width: 40px;
  height: 40px;
  animation: blow 1.5s linear infinite;
 
}

@keyframes blow {
    0%{
        transform: scale(0);
    }
    50%{
        transform: scale(1);
    }
    100%{
        transform: scale(0);
    }
}

.cube-1 {
  background: #0a41e4;
  animation-delay: 0;
}

.cube-2 {
  background: #ece909;
  animation-delay: 0.3s;
}
.cube-3 {
  background: #e40a0a;
  animation-delay: 0.6s;
}
.cube-4 {
  background: #3ecf05;
  animation-delay: 0.9s;
}

@media screen and (min-width:600px) and (orientation:landscape){
  .loading{
    width: 150px;
    height: 150px;
  }

  .cube{
    width: 75px;
    height: 75px;
  }
}