.fav-cities{
    width: 90%;
    height: 60%;
    overflow-y: auto;
    overflow-x: hidden;
    font-size: 3.5vmax;
    font-weight: 600;
    margin: 5px auto;
    text-align: center;
}

ul{
    list-style-type: none;
    
}

li{
    padding: 2% ;
    margin: 1% auto;
    background: #AAA;
    transition: 0.3s ease all;
    width:80% ;
    
}

@media screen and (min-width: 600px) and (orientation: landscape) {
    .fav-cities{
        overflow-x: hidden;
        overflow-y: auto;
        font-size: 2vmax;
    }

    li{
        
        cursor: pointer;
    }

    li:hover{
        background: #999;
    }
}